@import url(https://fonts.googleapis.com/css?family=Special+Elite);
@import url(https://fonts.googleapis.com/css?family=Nova+Flat);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

body {
    height: 100vh;
    background-color: whitesmoke;
}

#App{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
}

#main{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction:column;
            flex-direction:column;
    -webkit-align-items: stretch;
            align-items: stretch;
    min-width: 70%;
    padding-bottom: 15px;
    -webkit-flex-grow: 1;
            flex-grow: 1;
}

#headBar{
    background-image: url('https://png.pngtree.com/thumb_back/fw800/back_pic/02/65/07/60578709af35d56.jpg');
    font-family: 'Special Elite', cursive;
}


#headBar{
    width: 100%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    border-bottom: solid .8px gray;
}






#navBar{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    padding: 5px;
    color: white;
    background: #7e1c1c;
}

#navLeftBar, #navRightBar{
    display: -webkit-flex;
    display: flex;
}

#navBrandName{
    padding: 0 10px;
    font-size: 25px;
    font-family: 'Nova Flat', cursive;
}

.navButton{
    background: none;
    border: none;
}
#todoList{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 7px;
    background: white;
}

.todo{
    padding: 4px;
    display: -webkit-flex;
    display: flex;
    border-bottom: dashed 1px green;
}

.todo:hover{
    background: whitesmoke
}

.subTodo {
    border-bottom: dashed 1px green;
}

.todoTitle{
    padding: 0 12px;
    -webkit-flex-grow : 1;
            flex-grow : 1
}

.stateButton{
    border: none;
    background: none;
}

.stateButton:focus{
    outline: none
}
#sideBarTodo{
    /* border: solid .4px gray; */
    min-width: 30%;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-start;
            align-items: flex-start;
    
}

/* Media queries for mobile compatibilty */

@media only screen and (max-width: 800px){
    #sideBarTodo{
        
    position: absolute;
    background: #000000bd;
    right: 0;
    left: 0;
    bottom: 0;
    top: 40px;
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: flex-start;
            align-items: flex-start;

    }
}



#sideBarDefault{
    /* border: solid .4px gray; */
    display: none;
    /* min-width: 30%;
    display: flex;
    justify-content: center;
    align-items: center; */
    
}


.todoSideBarView{
    
    display: -webkit-flex;
    
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-flex-grow: 1;
            flex-grow: 1;

}

.todoSideBarViewHead{
    display: -webkit-flex;
    display: flex;
    padding: 20px 4px 4px 4px;
    border-bottom: solid 2px;
    background: white;

}

.todoSideBarViewHeadTitle{
    
    -webkit-flex-grow: 1;
    
            flex-grow: 1;
    padding-left: 6px;

}

.todoSideBarViewSteps{
    
    display: -webkit-flex;
    
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
    padding: 4px;
    margin: 8px;
    background: white;

}

.subTodo{
    display: -webkit-flex;
    display: flex;
    -webkit-align-items: center;
            align-items: center;
    padding: 2px;
}

.subTodo-card{
    -webkit-flex-grow: 1;
            flex-grow: 1;
    
}

form input{
    border: none;
    font-size: 15px;
    -webkit-flex-grow: 1;
            flex-grow: 1;
    background: none;
}

form input:focus{
    outline: none;
}

form {
    padding: 4px;
    border-bottom: dashed 1px green;
    display: -webkit-flex;
    display: flex;
    height: 31px;
}

.flagChooser{
    border: none;
}

.flagChooser:focus{
    outline: none;
}
